import saipan19 from "../../image/saipan/saipan(19).jpg";
import saipan20 from "../../image/saipan/saipan(20).jpg";
import saipan21 from "../../image/saipan/saipan(21).jpg";
import saipan22 from "../../image/saipan/saipan(22).jpg";
import saipan23 from "../../image/saipan/saipan(23).jpg";

const SaipanPage = () => {
  const [images] = useState([
    { src: saipan1, text: "!!.jpg" },
    { src: saipan2, text: "    .jpg" },
    { src: saipan3, text: "  .jpg" },
    { src: saipan4, text: " .jpg" },
    { src: saipan5, text: "2   .jpg" },
    { src: saipan6, text: "  .jpg" },
    { src: saipan7, text: " T-bone !.jpg" },
    { src: saipan8, text: "  .jpg" },
    { src: saipan11, text: "  .jpg" },
    { src: saipan12, text: " .jpg" },
    { src: saipan15, text: "  .jpg" },
    { src: saipan17, text: "(?).jpg" },

    { src: saipan18, text: "   .jpg" },
    { src: saipan19, text: "  .jpg" },
    { src: saipan20, text: " .jpg" },
    { src: saipan21, text: " .jpg" },
    { src: saipan22, text: "  .jpg" },
    { src: saipan23, text: " (feat.).jpg" }
  ]);
  return (
    <div className="TravelWrapper">
      <div className="TravelContainer">
        <TravelFolder Folder="saipan" />
        <TravelImage images={images} />
        <TravelReady />
      </div>
      <TravelFooter List="/travel" />
    </div>
  );
};

export default SaipanPage;
